<template>
  <div class="regist justify-space-between">
    <v-card class="mx-auto text-center" tile max-width="400px">
      <v-card-actions>
        <v-container>
          <v-row>
            <v-file-input
              v-model="csvInput"
              label="CSV File Input" accept=".csv,text/csv"
              @change="handleUpload" @click:clear="clearInput()"
            />
          </v-row>
          <v-row v-show="newItems.length > 0">
            <v-col>
              <v-btn v-on:click="saveData">データ上書き</v-btn>
            </v-col>
            <v-col>
              <v-btn v-on:click="clearInput()">キャンセル</v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card-actions>
      
      <!-- データ表示 -->
      <v-card class="mx-auto">
        <v-card-title>新しいデータ: {{ newItems.length }}件</v-card-title>
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th>No.</th>
                <th>名前</th>
                <th>フリガナ</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in newItems" v-bind:key="i">
                <td>{{ i+1 }}</td>
                <td>{{ item[0] }}</td>
                <td>{{ item[1] }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </v-card>
  </div>
</template>

<script>
import storage from "../util/Storage"
import Papa from "../util/papaparse.min.js"

export default {
  name: 'Regist',
  data(){
    return {
      newItems: storage.newItems || [],
      csvInput: null /* fileinputクリアのため */
    }
  },
  methods: {
    saveData(){ /* 読み込んだデータをDBに上書き */
        if(confirm("データを上書します")){
            storage.updateCurrentItems()
            alert("" + storage.currentItems.length + "件上書しました")
            this.clearInput()
        }
    },
    handleUpload(file){ /* CSVからデータを読込 */
        /* データ：2列, 空行はスキップ */
        if(file){
            const reader = new FileReader()
            const vm = this
            reader.onload = function(ev){
                const csv = ev.target.result
                const config = {
                  skipEmptyLines: true
                }
                const collection = Papa.parse(csv, config)
                storage.setNewItems(collection.data)
            }
            reader.readAsText(file)
        }
    },
    clearInput(){ /* 読み込んだデータをクリア */
      storage.clearNewItems()
      this.csvInput = null; /* fileinputをクリア */
    }
  }
}
</script>
<style scoped>
th, td {
  text-align: center;
}
</style>
